.Wrapper {
  background-image: url(../../../assets/Quality/Testimonials.png);
  background-repeat: no-repeat;
  background-size: 100%;
  padding: 10% 0;
  margin: 5% 0;
}
.texts {
  width: 40%;
  margin: 0 auto;
  color: white;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: flex-start;
}
.texts img {
  width: 50px;
  height: 50px;
}
.head {
  display: flex;
  align-items: center;
  gap: 20px;
}
.head p {
  margin: 0;
  padding: 0;
}
.icons {
  display: flex;
  align-items: center;
  gap: 10px;
}
.icons i {
  color: #ff9900;
}
.head img {
  width: 100px;
}
.div {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.subtext {
  font-family: "Montserrat";
  font-size: 30px;
  font-weight: 600;
  line-height: 49px;
  letter-spacing: -0.02em;
}
.user {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
  background-color: white;
  padding: 2%;
  border-radius: 20px;
}
.user p {
  margin: 0;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  color: black;
}
.user img {
  border-radius: 50%;
}
.name {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.name h3 {
  color: #0b3d2c;
  font-family: "Montserrat";
  font-size: 22px;
  font-weight: 600;
  letter-spacing: -0.02em;
}

@media screen and (max-width: 1310px) {
  .Wrapper {
    padding: 7% 0;
  }
  .subtext {
    font-size: 20px;
  }
}
@media screen and (max-width: 1030px) {
  .Wrapper {
    padding: 8% 0;
  }
  .subtext {
    font-size: 17px;
    line-height: 30px;
  }
}

@media screen and (max-width: 850px) {
  .Wrapper {
    padding: 15% 0;
    background-size: 700px;
  }
  .subtext {
    font-size: 20px;
  }
  .texts {
    width: 80%;
  }
  .head {
    display: none;
  }
  .subtext {
    margin: 0;
  }
}

@media screen and (max-width: 480px) {
  .texts {
    width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  .subtext {
    font-size: 20px;
  }
  .user {
    margin: 0 auto;
  }
}
