.Wrapper p {
  display: flex;
  align-items: center;
  gap: 20px;
  justify-content: center;
}
.Wrapper p span {
  color: #2a7d2e;
  font-family: "Montserrat";
  font-size: 22px;
  font-weight: 600;
  letter-spacing: -0.02em;
}
.Wrapper h2 {
  text-align: center;
  color: #0b3d2c;
  font-family: "Montserrat";
  font-size: 45px;
  font-weight: 700;
  letter-spacing: -0.02em;
}
.cards {
  display: flex;
  justify-content: space-between;
  gap: 30px;
  margin: 5% 0;
  padding: 2%;
}
.userimage {
  width: 50px;
  height: 50px;
}
.image {
  width: 100%;
}
.card {
  background-color: white;
  box-shadow: 0px 7px 60px 0px #00000012;
  border-radius: 20px;
  padding: 2%;
  cursor: pointer;
  transition: 0.65s;
}
.card:hover {
  transform: scale(1.05);
}
.date {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fcfaf7;
  border: 1px solid #0b3d2c33;
  padding: 2% 4%;
  border-radius: 20px;
}
.date p {
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
  color: #2f2f2f;
}
.h33 {
  color: #0b3d2c;
  font-family: "Montserrat";
  font-size: 20px;
  margin: 5% 0;
  font-weight: 600;
  letter-spacing: -0.02em;
  line-height: 30px;
}
.user {
  display: flex;
  align-items: center;
  gap: 20px;
  margin: 2% 0;
}
.userimage {
  border-radius: 50%;
}
.image {
  border-radius: 20px;
  height: 300px;
  margin-top: 3%;
}
@media screen and (max-width: 1000px) {
  .cards {
    display: flex;
    flex-direction: column;
  }
  .Wrapper h2 {
    font-size: 30px;
  }
}
