.WrapperBack {
  background-color: white;
}

.Wrapper {
  padding: 1% 0;
  display: flex;
  align-items: center;

  justify-content: space-between;
}
.Wrapper .Ul {
  display: flex;
  align-items: center;
  justify-content: flex-end !important;
  margin-left: auto;

  gap: 50px;
}
.Wrapper p,
h3 {
  margin: 0;
}
.Wrapper a {
  text-decoration: none;
}
.Wrapper .Ul .li {
  display: flex;
  align-items: center;
  width: 100% !important;
}
.Wrapper ion-icon {
  color: #0b3d2c;
  width: 30px;
  height: 20px;
}
.Wrapper h3 {
  color: #0b3d2c;
  font-size: 14px;
  font-weight: 600;
  width: 100%;
}

.icon {
  background-color: #f1d2a9;
  border-radius: 50px;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icons {
  background-color: #f1d2a9;
  border-radius: 50px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.texts {
  margin-left: 5%;
  display: flex;
  flex-direction: column !important;
  align-items: flex-end !important;
  /* width: 100%; */
}

.Wrapper .li:nth-child(4) {
  width: 30% !important;
}

.Wrapper .li:nth-child(5) {
  width: 30% !important;
}
.Logo {
  display: none;
}

.iconsM {
  display: none;
}
.media {
  display: none;
}

@media screen and (max-width: 1340px) {
  .Wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .Wrapper .Ul {
    overflow: scroll !important;
    /* width:  !important; */
  }
  .Wrapper .li {
    width: 100% !important ;
  }

  .Logo {
    display: block;
    border-radius: 100px;
    background-color: #2a7d2e;
    padding: 2%;
    width: 30%;
    height: 11%;
  }

  .Logo img {
    width: 100% !important;
  }
}

@media screen and (max-width: 1140px) {
  .Wrapper {
    padding: 2% 2%;
  }
  .Ul {
    display: none !important;
  }
  .iconsM {
    display: block;
    background-color: #f1d2a9;
    border-radius: 50px;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
}

@media screen and (max-width: 660px) {
  .Logo {
    width: 50%;
    height: 20%;
  }
}

@media screen and (max-width: 500px) {
  .Wrapper {
    display: none;
  }
}

.WrapperBack2 {
  background-color: #2a7d2e;
}
.WrapperBack2 img {
  width: 300px;
}
.Wrapper2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5% 0;
  width: 100% !important;
}
.Wrapper2 ul {
  display: flex;
  align-items: center;
  gap: 50px;
}
.Wrapper2 ul li a {
  color: white;
  text-decoration: none;
  font-family: "Montserrat";
  font-size: 18px;
  font-weight: 500;
  line-height: 22px;
  letter-spacing: 0em;
}
.Wrapper2 ul select {
  background-color: transparent;
  border: none;
  color: white;
  font-family: "Montserrat";
  font-size: 17px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0em;
}
