.WrapperBack {
  background-color: #fcfaf7;
}
.Wrapper {
  text-align: center;
  padding-top: 5%;
}
.Wrapper p span {
  color: #2a7d2e;
  font-family: "Montserrat";
  font-size: 22px;
  font-weight: 600;
  letter-spacing: -0.02em;
}
.Wrapper p {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.Wrapper h2 {
  color: #0b3d2c;
  font-family: "Montserrat";
  font-size: 45px;
  font-weight: 700;
  letter-spacing: -0.02em;
}
.cards {
  display: flex;
  justify-content: space-around;
  gap: 30px;
  padding: 2%;
}

.card {
  background-color: white;
  padding: 2%;
  border-radius: 30px;
  color: #0b3d2c;
  width: 25%;
  cursor: pointer;
  text-align: center;
  box-shadow: 0px 7px 60px rgba(0, 0, 0, 0.07);
  transition: 0.65s;
}
.card:hover {
  background-color: #0b3d2c;
  color: white;
  transform: scale(1.05);
}
.card:hover .icon {
  background-color: white;
  color: #0b3d2c;
}
.card:hover button {
  background-color: #f1d2a9;
  color: #0b3d2c;
  border: 1px solid #f1d2a9;
}
.card img {
  border-radius: 20px;
  height: 130px;
  width: 100%;
}
.firsth3 {
  font-family: "Montserrat";
  font-size: 24px;
  font-weight: 600;
  letter-spacing: -0.02em;
  margin: 5% 0;
  border-bottom: 1px solid #0b3d2c1a;
  padding-bottom: 6%;
}
.card ul li {
  display: flex;
  align-items: center;
  gap: 30px;
}
.icon {
  background-color: #2a7d2e;
  color: white;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-size: 20px;
  transition: 0.65s;
}
.card ul li p {
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 0em;
}
.card h2 {
  font-family: "Montserrat";
  font-size: 45px !important;
  font-weight: 600;
  margin: 5% 0;
  letter-spacing: -0.02em;
}
.card h2 span {
  font-size: 15px;
}
.card button {
  background-color: #0b3d2c;
  color: white;
  font-family: "Montserrat";
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0em;
  padding: 15px 0px;
  width: 100%;
  border-radius: 20px;
  border: 1px solid #0b3d2c;
  transition: 0.65s;
}
.card h4 {
  padding-top: 5%;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0em;
}

@media screen and (max-width: 1100px) {
  .cards {
    display: flex !important;
    flex-direction: column !important;
    align-items: center;
    gap: 30px;
    justify-content: center;
  }
  .card {
    width: 60%;
  }
  .card img {
    height: 200px;
  }
}

@media screen and (max-width: 550px) {
  .Wrapper h2 {
    font-size: 30px;
    padding-bottom: 5%;
  }
  .card {
    width: 90%;
  }
}
