.WrapperBack {
  background-color: #0b3d2c;
}
.Wrapper {
  padding: 13% 2% 5%;
}
.Wrapper a {
  color: white;
  text-decoration: none;
  width: 100%;
}
.subtext {
  font-size: 19px;
  font-size: 500;
}
.Wrapper ul {
  display: flex;
  justify-content: space-between;
  gap: 30px;
  margin: 0;
  padding: 0;
  color: white;
}
.headNumbers {
  margin: 4% 0 3%;
}
.numbers {
  margin: 0 0 2%;
}
.imgs {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 5%;
}
.imgs img {
  width: 50px;
  height: 50px;
  border-radius: 10px;
}

.img img {
  width: 100px;
}
.img {
  background-color: #2a7d2e;
  padding: 2%;
  width: 300px;
  height: 100px;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Wrapper ul li {
  width: 33% !important;
  margin: 0 auto;
}
.Wrapper ul li:nth-child(1) {
  width: 43%;
}
.Wrapper ul li:nth-child(4) {
  width: 20%;
}
.Wrapper p {
  width: 80%;
}
.icons {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 25px;
}
.icon {
  border: 2px solid #ffffff1a;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.65s;
  cursor: pointer;
}
.icon:hover {
  background-color: #2a7d2e;
}
.Wrapper ul li h3 {
  border-bottom: 2px solid #f1d2a9;
  padding-bottom: 5%;
}
.Wrapper ul li {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.Bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  padding: 2.5% 2%;
  position: relative;
  border-top: 1px solid #f1d2a9;
}
.Bottom ul {
  display: flex;
  align-items: center;
  gap: 50px;
}

.icona {
  position: absolute;
  top: -35%;
  left: 47.5%;
  background-color: white;
  color: black;
  cursor: pointer;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.icona a {
  background-color: #f1d2a9;
  color: black;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
}

@media screen and (max-width: 1250px) {
  .Wrapper {
    padding: 20% 2% 10%;
  }
  .icona {
    top: -60%;
    left: 40%;
  }
}
@media screen and (max-width: 900px) {
  .Wrapper ul {
    display: flex;
    flex-direction: column;
    align-items: flex-start !important;
    justify-content: flex-start !important;
    width: 100%;
  }
  .Wrapper ul li {
    width: 100% !important;
  }
  .Bottom ul {
    gap: 10px;
  }
}

@media screen and (max-width: 750px) {
  .Bottom {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }
}

@media screen and (max-width: 500px) {
  .img {
    margin-top: 6%;
    width: 170px;
    height: 60px;
    background-color: #f1d2a9;
  }
  .img img {
    width: 60px;
  }
}
