.Logo {
  position: absolute;
  border-radius: 100px;
  left: 0;
  background-color: #2a7d2e;
  padding: 2%;
  width: 260px;
  height: 80px;
  top: 11%;
  left: 4%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Logo img {
  width: 130px !important;
}
@media screen and (max-width: 1340px) {
  .Logo {
    display: none;
  }
}
