.Wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  flex-wrap: wrap;
  border-top: 1px solid grey;
  padding: 5% 2% 0;
  margin: 5% 0;
}

@media screen and (max-width: 500px) {
  .Wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
  }
}
