.WrapperBack {
  background-image: url(../../../assets/HomeHero/back.png);
  background-repeat: no-repeat;
  background-size: cover;
}
.Wrapper {
  text-align: center;
  color: white;
  padding: 4% 0 10%;
}
.Wrapper2 {
  width: 100%;
  padding: 5% 0;
}

.Wrapper p {
  font-size: 24px;
  font-weight: 600;
  margin: 0;
}
.Wrapper h2 {
  font-size: 100px;
  margin: 0;
  padding-bottom: 1%;
}
.Wrapper .subtext {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  margin: 0 auto;
}
.subtext div {
  background-color: white;
  width: 70px;
  height: 2px;
}
.Buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  padding: 3% 0 0;
  margin: 0 auto;
}
.Buttons a  button {
  color: black;
  background-color: #f1d2a9;
  padding: 15px 25px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  font-style: normal !important;
  border-radius: 20px;
  border: 1px solid #f1d2a9;
  cursor: pointer;
}
