@media screen and (max-width: 870px) {
  .Wrapper h2 {
    font-size: 100px;
  }
}
@media screen and (max-width: 570px) {
  .Wrapper h2 {
    font-size: 35px !important;
  }
  .Wrapper p {
    font-size: 15px;
  }
  .Wrapper {
    padding-left: 2%;
    padding-right: 2%;
  }
  .subtext {
    gap: 10px !important;
  }
}

@media screen and (max-width: 370px) {
  .Buttons button {
    font-size: 13px !important;
    padding: 10px 14px !important;
  }
  .subtext div {
    width: 50px;
  }
}
