.Wrapper {
  background: #0b3d2c;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.8% 3% 1%;
  border-end-start-radius: 30px;
  border-end-end-radius: 30px;
}
.Wrapper p {
  margin: 0;
}
.Wrapper p span {
  border-bottom: 1px solid white;
  font-weight: 600;
}
.Wrapper a {
  color: white !important;
  text-decoration: none;
}

.icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 10%;
}
.icons i {
  font-size: 20px;
  cursor: pointer;
}

@media screen and (max-width: 660px) {
  .Wrapper {
    padding: 3% 2%;
    display: flex;
    gap: 20px;
    flex-direction: column;
  }
  .Wrapper p {
    text-align: center;
  }
  .Wrapper .icons {
    width: 40%;
  }
}

@media screen and (max-width: 500px) {
  .Wrapper {
    display: none;
  }
}
