.Wrapper {
  width: 100%;
  padding:5% 0;
}
.texts {
  margin-top: 5%;
}
.texts h2 {
  font-family: "Montserrat";
  font-size: 65px;
  font-weight: 700;
  letter-spacing: -0.02em;
  color: white;
  text-align: center;
}
.buttons {
  background: #0b3d2cd9;
  width: 50%;
  margin: 0 auto;
  border-start-end-radius: 20px;
  border-start-start-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  color: white;
  margin-top: 10%;
}
.buttons p {
  font-size: 22px;
  font-weight: 600;
  letter-spacing: 0em;
}
.meida {
  display: none !important;
}
@media screen and (max-width: 940px) {
  .Wrapper ul {
    display: none;
  }
  .Wrapper ion-icon {
    display: block;
  }
  .Wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 20px;
  }
  .icon {
    background-color: #f1d2a9;
    border-radius: 50px;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  .Wrapper ion-icon {
    color: #0b3d2c;
    font-size: 25px;
    font-weight: bold;
  }
  .Wrapper {
    padding: 2%;
  }
}

@media screen and (max-width: 610px) {
  .buttons {
    width: 80%;
  }
}

@media screen and (max-width: 500px) {
  .texts h2 {
    font-size: 30px;
  }
  .buttons p {
    font-size: 15px;
  }
  .meida {
    display: flex !important;
    align-items: center;
    justify-content: space-between !important;
    padding: 2% 2%;
  }
  .meida img {
    width: 160px;
  }

  .img {
    background-color: #2a7d2e;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 80px;
    border-radius: 50px;
  }
  .Wrapper {
    display: contents;
  }
}
